import { FormOptions } from '@/declareTs/form';
import { User, Lock } from '@element-plus/icons-vue'
import { markRaw } from 'vue'
// 登录表单配置
export const formOptions: FormOptions = {
    inline: false,
    labelWidth: 0,
    size: "large",
    rules: {
        name: { required: true },
        password: { required: true },
    },
    items: [
        {
            controlType: "input",
            placeholder: "搜索账号",
            prefixIcon: markRaw(User),
            keyName: "name"
        },
        {
            controlType: "input",
            type: 'password',
            showPassword: true,
            prefixIcon: markRaw(Lock),
            placeholder: "搜索账号",
            keyName: "password"
        }
    ],
};

// 手机登录表单配置
export const phoneFormOptions: FormOptions = {
    inline: false,
    labelWidth: 0,
    size: "large",
    rules: {
        phone: { required: true },
        code: { required: true },
    },
    items: [
        {
            controlType: "input",
            placeholder: "填写手机号",
            prefixIcon: markRaw(User),
            keyName: "phone"
        },
        {
            slot: "code",
            keyName:'code'
        }
    ],
};